.modal-overlay{position:fixed;inset:0;background:rgba(0,0,0,.5);z-index:1000;display:flex;align-items:center;justify-content:center;padding:1rem;backdrop-filter:blur(3px)}.modal-content{background:var(--card-bg);color:var(--text);border:1px solid var(--card-border);border-radius:12px;padding:2rem;width:100%;max-width:500px;box-shadow:0 12px 24px rgba(0,0,0,.15);position:relative;font-family:inherit}.modal-content h2{margin-top:0;font-size:1.5rem;font-weight:700;margin-bottom:1.5rem;text-align:center}.modal-content label{display:block;margin-bottom:.25rem;font-weight:600;font-size:.9rem}.modal-content input,.modal-content textarea{width:100%;padding:.75rem;border:1px solid var(--card-border);border-radius:6px;font-size:1rem;background:var(--bg);color:var(--text);margin-bottom:1rem}.modal-content input:focus,.modal-content textarea:focus{outline:2px solid var(--accent);border-color:var(--accent)}.modal-content textarea{resize:vertical;min-height:100px}.modal-content button[type=submit]{background:var(--accent);color:#fff;border:none;border-radius:6px;padding:.75rem 1.5rem;font-weight:600;font-size:1rem;cursor:pointer;transition:background .3s ease;display:block;margin:0 auto}.modal-content button[type=submit]:hover,.modal-content button[type=submit]:focus{background:var(--accent-hover, #00ffff)}.modal-close{position:absolute;top:1rem;right:1rem;background:rgba(0,0,0,0);border:none;font-size:1.5rem;color:var(--text-subtle);cursor:pointer}.modal-close:hover{color:var(--text)}.modal-close:focus{outline:2px solid var(--accent);border-radius:50%}